@import "main";

:root {
  --background-color:#C4CE80;
  --section-color: #B1B787;
  --project-color: #CED3AF;
}

html,body{
  font-family:"Trebuchet MS", Helvetica, sans-serif;
  text-align:center;
  min-width:260px;
  color:#333;
  background: var(--background-color);
}
.section{
  max-width: 720px;
  margin:15px auto;
  padding:10px;
  border:0px solid;
  border-radius:5px;
  background:var(--section-color);
}
.project{
  margin:5px;
  padding:5px;
  border:0px solid;
  border-radius:5px;
  background:var(--project-color);
}
img{
  width: 100%;
}
.image-wrapper{
  margin: auto;
  border-radius:5px;
  padding-top: 1px;
  background:var(--project-color);
  width: 70%;
  height: auto;
}

#headline{
  margin-top:50px;
  text-align:left;
}
ul{
  max-width:550px;
  margin:0 auto;
  text-align:left;
}
li{
  margin: 10px;
}
blockquote{
  font-style: italic;
}

* {
  box-sizing: border-box;
}

.column {
  float: left;
  padding: 10px;
}
.left_image{
  margin: auto;
  border-radius:5px;
  padding: 5px;
  background:var(--project-color);
}
.left {
  width: 25%;
}

.right {
  width: 75%;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}
a:link {
  color: #333333;
}

a:visited {
  color: #333333;
}


#header-buttons {
  display: table;
  table-layout: fixed;
  width:100%;
}
#header-buttons div {
  display: table-cell;
}
.post-meta{
  font-size: 14px;
}
.btn {
  border: none;
  background-color: inherit;
  font-size: 16px;
  cursor: pointer;
  display: inline-block;
  margin:5px;
  padding:5px;
  border-radius:5px;
  text-decoration: none;
  background:var(--project-color);
  font-family:"Trebuchet MS", Helvetica, sans-serif;
  text-align:center;
  color:#333;
  width:80%;

}
